import styled from 'styled-components';

export const AppWrapper = styled.div`
  background-color: ${({ theme }) => theme.colors.mainBGC};
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  ${({ theme }) => theme.media.desktop} {
    display: grid;
    grid-template-rows: 90px 1fr;
    grid-template-columns: 150px 1fr;
  }
`;
