import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {
  MenuWrapper,
  MenuButton,
  MenuButtonSpan,
  LogoWrapper,
  StyledLink,
  LogoutButton,
} from './Menu.style';
import { ReactComponent as Logo } from 'assets/icons/logo.svg';
import { logout } from 'actions/authActions';
import { useDispatch } from 'react-redux';

const Menu = (props) => {
  const dispatch = useDispatch();
  const [button, setButton] = useState(false);
  const openMenu = () => setButton((prevState) => !prevState);
  const openMenuClass = button === true ? 'openMenuClass' : null;
  
  return (
    <>
      <MenuButton onClick={openMenu}>
        menu <MenuButtonSpan menuTransition={button} />
      </MenuButton>
      <MenuWrapper className={openMenuClass}>
        <LogoWrapper>
          <Logo />
        </LogoWrapper>
        <StyledLink exact to="/">
          Dashboard
        </StyledLink>
        <StyledLink to="/training">Trening</StyledLink>
        <StyledLink to="/options">Ustawienia</StyledLink>
        <LogoutButton onClick={() => dispatch(logout())}>Wyloguj</LogoutButton>
      </MenuWrapper>
    </>
  );
};

Menu.propTypes = {};

export default Menu;
