import styled from 'styled-components';
import { NavLink } from 'react-router-dom';

export const MenuWrapper = styled.nav`
  width: 100%;
  height: 100vh;
  background-color: ${({ theme }) => theme.colors.mainBGC};
  transition: transform 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: translateX(100%);
  z-index: 999;
  &.openMenuClass {
    transform: translateX(0);
  }
  ${({ theme }) => theme.media.desktop} {
    transform: translateX(0);
    position: static;
    grid-row: 1 / 3;
    grid-column: 1 / 1;
  }
`;

export const MenuButton = styled.button`
  position: absolute;
  top: 30px;
  right: 20px;
  padding: 15px;
  color: ${({ theme }) => theme.colors.mainDark};
  font-size: ${({ theme }) => theme.fontSize.s};
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  outline: none;
  z-index: 1000;
  ${({ theme }) => theme.media.desktop} {
    display: none;
  }
`;

export const MenuButtonSpan = styled.span`
  width: 28px;
  background-color: black;
  height: 2px;
  margin-left: 10px;
  position: relative;
  border-radius: 5px;
  background-color: ${({ menuTransition, theme }) =>
    menuTransition === true ? 'transparent' : theme.colors.mainDark};
  transition: background-color 0.2s ease;
  &::before,
  &::after {
    position: absolute;
    content: '';
    height: 100%;
    background-color: ${({ theme }) => theme.colors.mainDark};
    right: 0;
    border-radius: 5px;
    transition: transform 0.2s ease;
    transform-origin: ${({ menuTransition }) =>
      menuTransition === true ? 'center' : 'right'};
  }
  &::after {
    width: 100%;
    top: 0;
    transform: ${({ menuTransition }) =>
      menuTransition === true
        ? 'scaleX(100%) translateY(0) rotate(-45deg)'
        : 'translateY(8px) scaleX(60%)'};
  }
  &::before {
    width: 100%;
    top: 0;
    transform: ${({ menuTransition }) =>
      menuTransition === true
        ? 'scaleX(100%) translateY(0) rotate(45deg)'
        : 'translateY(-8px) scaleX(75%)'};
  }
`;

export const LogoWrapper = styled.div`
  position: absolute;
  top: 13px;
  left: 30px;
  width: 110px;
  svg {
    width: 100%;
    height: 90px;
  }
`;

const activeClassName = 'active-link';
export const StyledLink = styled(NavLink).attrs({ activeClassName })`
  cursor: pointer;
  text-decoration: none;
  color: ${({ theme }) => theme.colors.mainDark};
  position: relative;
  font-size: ${({ theme }) => theme.fontSize.headers};
  font-weight: 400;
  margin: 20px auto;
  &.${activeClassName} {
    color: ${({ theme }) => theme.colors.mainColor};
    &::before {
      opacity: 1;
    }
  }
  &::before {
    content: '';
    position: absolute;
    width: 15px;
    height: 3px;
    left: -20px;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: ${({ theme }) => theme.colors.mainColor};
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
`;

export const LogoutButton = styled.button`
  border: none;
  outline: none;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 20px 30px;
  border-radius: 8px;
  margin-top: 20px;
  font-size: ${({ theme }) => theme.fontSize.m};
`;
